    <div class="title">三、我们如何共享、转让、公开披露您的个人信息</div>
    <div class="text">
      1.<b>共享</b>
<div class="text de-2 blod">通常情况下，我们不会与任何公司、组织、个人共享您的个人信息。但是，为了向您提供更完善、优质的产品/服务，我们可能会在遵守“合法正当、最小必要、目的明确原则”的前提下，与第三方合作伙伴共享您的某些个人信息。此时，我们会与合作伙伴签署严格的保密协定，要求他们按照我们的说明、本隐私政策以及其他任何相关的保密和安全措施来处理您的个人信息，如果您拒绝我们的合作伙伴在提供服务时收集为提供服务所必须的个人信息，将可能导致您无法在我们的平台中使用该第三方服务。涉及以下情况我们可能会共享您的信息：</div>
1.1.为我们的产品/服务提供功能支持的服务提供商：例如提供支付服务的支付机构、提供内容发布平台的服务商以及其他服务提供商，我们共享信息的目的仅为实现产品/服务的功能；
<br>1.2.在法定情形下的共享：我们可能会根据法律法规规定、诉讼、仲裁解决需要，或按行政、司法机关依法提出的要求，对外共享您的用户信息；
<br>1.3.与关联公司间共享：为了与我们的其他产品或服务所关联，为您提供更好的服务，您的信息可能会在锋伟网络的关联公司内共享。我们仅会处于特定、明确而合法的且在本政策所声明的保护措施，并且严格遵守相关法律法规与监管要求。关联公司如要改变个人信息的处理目的的，将需要再次征求您的授权同意。
  <br>1.4.第三方SDK
  <br>1.4.1.我们的产品与/或服务中可能会包含第三方SDK，如您在我们平台使用这类由第三方提供的服务时，您同意将由其直接收集和处理您的信息。
  <br>1.4.2.我们会对授权合作伙伴获取有关信息的应用程序接口（API）、软件工具开发包（SDK）进行严格的安全检测，以保护您的个人信息安全。
  <br>1.4.3.<b>目前我们产品中包含的第三方API服务为</b>：ReechoAI睿声声音克隆服务（深圳市言域科技有限公司），为满足用户声音克隆的场景会收集用户设备信息、网络状态等；
  <br>2.<b>转让</b>
  <br>2.1.除获取您的明示同意外，我们不会将您的个人信息转让给任何公司、组织或个人。
  <br>2.2.涉及以下情况时，我们会转让相关信息给转让方，但如接收方变更本隐私政策中约定的个人信息处理目的、处理方式，我们将要求接收方重新获取您的授权同意：
  <br>2.2.1.在获得您明确同意的情况下转让；
  <br>2.2.2.如因合并、收购或破产清算的情形需要转移您的个人信息，我们会向您告知接收方的名称或者姓名，并要求接收方继续受本隐私政策的约束；
  <br>2.2.3.根据适用的法律法规、法律程序的要求、强制性的行政或司法要求所必须的情况下进行转让。
  <br>3.<b>公开披露</b>
  除符合法律法规的规定进行公开披露或获得您单独同意外，我们不会公开披露您未自行公开或者其他未合法公开的个人信息。如需公开披露您的个人信息，我们会提前进行个人信息保护影响评估，并采用符合行业内标准的安全保护措施和手段保护您的个人信息安全。
  <br>4.<b>共享、转让、公开披露您的个人信息的例外
  请您知悉，根据法律法规的规定，在下述情况下，共享、转让、公开披露您的个人信息无需事先征得您的授权同意：
  <br>4.1.为订立、履行我们与您签署的合同所必需；
  <br>4.2.为履行法定职责或者法定义务所必需，例如与国家安全、国防安全、与刑事侦查、起诉、审判和判决执行等直接相关的法定职责或者法定义务；
  <br>4.3.为应对突发公共卫生事件，或者紧急情况下为保护自然人的生命健康和财产安全所必需；
  <br>4.4.在合理的范围内处理个人自行公开或者其他已经合法公开的个人信息；
  <br>4.5.法律法规规定的其他情形。</b>
