    <div class="title">六、我们如何存储与保护您的个人信息</div>
    <div class="text">
      1.我们非常重视用户的隐私和个人信息保护，也已采取合理的措施保护用户的个人信息。除法律法规规定或本政策另有约定的情形外，我们将运用各种安全技术和程序建立完善的管理制度来保护您的个人信息，以免遭受未经授权的访问、使用或披露。未经用户许可我们不会向第三方公开、透露用户个人信息，我们会对相关信息采用专业加密存储与传输方式，保障用户个人信息的安全。
      <br>2.<b>存储地点：我们依照法律法规的规定，仅会将收集到的有关您的个人信息存储于中华人民共和国境内。</b>目前我们暂时不存在跨境存储您的个人信息或向境外提供个人信息的场景。如果确有必要进行数据的跨境传输，我们会单独向您明确告知（包括数据出境的目的、接收方、使用方式与范围、使用内容、安全保障措施、安全风险等），并征得您的授权同意，我们会确保数据接收方有充足的数据保护能力来保护您的个人信息。
      <br>3.<b>存储期限：我们在为您提供满足产品/服务目的所必需且最短的期间内保留您的个人信息，</b>例如：当您使用我们的注册及会员功能时，我们需要收集您的手机号码，且在您提供后并在您使用该功能期间，我们需要持续保存您的手机号码，以向您正常提供该功能、保障您的账号和系统安全。此外，我们或对您的相关信息保存至相关法律所规定的必要期限（例如《电子商务法》规定：商品和服务信息、交易信息保存时间自交易完成之日起不少于三年；《移动互联网服务信息暂行办法》第七条规定：记录用户日志信息，并保存六十日）。
      <br>4.<b>在超出上述期限，或您主动要求删除您的个人信息或注销您的账号后，我们将根据适用法律法规的要求尽快删除您的个人信息，除非可适用的法律法规或国家规范性文件要求在更长的期间内保留这些信息。
      <br>5.您了解并知悉，如您离开锋伟网络，前往第三方提供的浏览页面或其他产品/服务，我们将没有能力和直接义务保护您向第三方提交的任何个人信息，无论您登陆、浏览或使用上述产品是否基于锋伟网络的链接或引导。但我们会提示您注意将进入第三方产品或服务，以便您更好地保护您的个人信息。
      <br>6.请您理解，由于技术的限制以及可能存在的各种恶意手段，我们无法始终保证信息百分之百的安全，但我们将竭尽所能提供安全措施保障您提供给我们的个人信息的安全性。</b>
    </div>
    <br>
    <div class="title">七、您如何管理您的个人信息</div>
    <div class="text">
      1.您有权访问您的个人信息：
      <br>1.1.您有权通过相关产品页面随时登陆您的个人中心，以访问您账号中的个人资料信息；
      <br>1.2.您有权通过相关产品页面随时查阅您的使用信息；
      <br>1.3.其他信息：如您在访问过程中遇到操作问题需获取其他签署无法货值的个人信息内容，可通过本隐私政策提供的方式联系我们。
      <br>2.您有权更正、修改、删除您的个人信息：
      <br>2.1.当您发现您提供给我们的个人信息存在登记错误、不完整或有更新的，您可在我们的产品/服务中更正/修改您的个人信息。
      <br>2.2.对于您的部分个人信息，您也可以自行通过我们提供的相关产品/服务的功能页面，主动删除您提供信息。
      <br>3.注销账号
      <br>3.1.您有权注销您的锋伟网络账户，并根据本隐私协议所述的方式联系我们的客服协助您注销账号。<b>当您注销账号后，您将无法再以该账号登录和使用我们的产品与服务，且该账号下已产生但未消耗完毕的权益以及未来的逾期利益等全部权益将被清楚。
      <br>3.2.该账号下的内容、信息、数据、记录等将会被删除或匿名化处理（但法律法规另有规定或监管部门另有要求的除外，如依据《中华人民共和国网络安全法》规定，您的网络操作日志将至少保留六个月的时间）；锋伟网络账号注销完成后，将无法恢复。
      <br>3.3.我们会在收到您的注销申请，并在验证您的用户身份后的15个工作日内尽快向您回复并注销账号。</b>
    </div>
    <br>

    <div class="title">八、我们如何保护未成年人的个人信息</div>
    <div class="text">
      1.您有权访问您的个人信息：
      <br>1.锋伟网络非常重视对未成年人个人信息的保护，我们将根据国家相关法律法规的规定保护未成年人的个人信息。
      <br>2.<b>我们主要面向成人提供产品和服务。如您为未成年人，我们要求您请您的父母或监护人仔细阅读本隐私权政策，并在征得您的父母或监护人同意的前提下使用我们的服务或向我们提供信息。
      <br>3.对于经父母或监护人同意使用我们的产品或服务而收集未成年人个人信息的情况，我们只会在法律法规允许、父母或监护人明确同意或者保护未成年人所必要的情况下使用、共享、转让或披露此信息。</b>
    </div>
    <br>

<div class="title">九、我们如何更新本隐私政策</div>
    <div class="text">
      1.为了给您提供更好的服务，我们会根据产品的更新情况及法律法规的相关要求更新本隐私政策的条款，该等更新构成本隐私政策的一部分。未经您明确同意，我们不会削减您按照本政策所应享有的权利。
      <br>2.我们将在更新后的本隐私政策生效前向您以弹窗、发送推送信息或以其他方式通知您，<b>如该等更新造成您在本隐私政策下权利的实质减少或重大变更，我们会再次征求您的明示同意。</b>
      <br><b>3.重大变更包括但不限于：</b>
      <br>3.1.我们的服务模式发生重大变化。如处理个人信息的目的、处理的个人信息类型、个人信息的使用方式等；
      <br>3.2.我们在所有权结构、组织架构等方面发生重大变化。如业务调整、破产并购等引起的所有者变更等；
      <br>3.3.个人信息共享、转让或公开披露的主要对象发生变化；
      <br>3.4.我们负责处理个人信息安全的责任部门、联络方式及投诉渠道发生变化时；
      <br>3.5.您参与个人信息处理方面的权利及其行使方式发生重大变化；
      <br>3.6.个人信息安全影响评估报告表明存在高风险时；
      <br>3.7.伴随政策或法律发布实施，为响应国家政策或者法律规范，做出变更。
    </div>
    <br> 

    <div class="title">十、如何联系我们</div>
    <div class="text">
      <b>如您在使用锋伟网络产品/服务过程中对本政策或对您的个人信息有任何疑问，可通过以下方式联系我们：
      <br>公司名称：深圳市锋伟网络科技有限公司
      <br>联系邮箱：yuzhouwake@foxmail.com
      <br>联系地址：深圳市南山区南头街道前海社区桃园路283号港湾丽都裙楼201-D30</b>
    </div>
    <br>  
    <div class="title">十一、其他</div>
    <div class="text">
      1.<b>本隐私政策的解释及争议解决均适用中华人民共和国大陆地区法律。任何因本隐私政策以及我们处理您个人信息事宜引起的争议，您在此同意向深圳市锋伟网络科技有限公司注册地所在有管辖权的人民法院提起诉讼解决。</b>
2.本隐私政策的标题仅为方便及阅读而设，并不影响正文其中任何规定的含义或解释。
    </div>
    <br> 

    <div class="title">十二、名词解释</div>
    <div class="text">
      1.用户：是指注册、登陆以及使用锋伟网络提供的产品与/或服务并通过启动、浏览或上传数据至锋伟网络的用户，在本隐私政策更多地称为更多地称为“您”。
<br>2.账号：指您完成真实信息注册后，享有的用户账号，该用户账号由您负责保管；您应当对以您账号下进行的所有行为负法律责任。
<br>3.个人信息：个人信息是以电子或者其他方式记录的与已识别或者可识别的自然人有关的各种信息，不包括匿名化处理后的信息。为免疑义，个人信息包括但不限于敏感个人信息。
<br>4.设备信息：通常包括设备名称、设备型号、硬件序列号、MAC地址、设备标识信息（IMEI/MEID/AndroidID/OpenUDID/IMSI/IDFA/IDFV/OAID/ICCID及其他综合设备参数及系统信息形成的设备标识符）、移动应用列表等软硬件特征信息、操作系统和应用程序版本、语言设置、分辨率、服务提供商网络ID（PLMN）、IP地址、浏览器类型。
    </div>
    <br>         
    </div>
  </div>
</template>

